// Стили для выбора цвета в админке
.colorpalette {
  .dropdown-menu {
    flex-wrap: wrap;
    width: auto;
    padding: 0;
    cursor: pointer;
  }
  &.show {
    .dropdown-menu {
      display: flex;
    }
  }
  .colorName {
    width: 100%;
    display: flex;
  }
  .dropdown-item {
    width: 20px;
    height: 20px;
    padding: 0;
    margin: 2px;
    transition: .2s;
    &:hover {
      box-shadow: 0 0 2px #555;
    }
  }
  .colorpaletteBtn   {
    width: 40px;
  }
}
